/** @jsx jsx */
import { jsx } from '@ali/4ever-dev-test';
import { Controller, Value } from '@ali/4ever-cangjie';
import { getActiveState, ListTypeEnum } from '@ali/4ever-plugin-list/src/bi/components/createListMenu';
import createBiPlugins from '../../createBiPlugins';

const plugins = createBiPlugins().array;

describe('list menubar', () => {
  [
    {
      title: '选中任务列表',
      data: {
        list: {
          listId: 'dn7ncrq6m7j',
          level: 0,
          isOrdered: false,
          isTaskList: true,
          isChecked: false,
        },
      },
      type: ListTypeEnum.tlist,
    },
    {
      title: '选中有序列表',
      data: {
        list: {
          listId: 'dn7ncrq6m7j',
          level: 0,
          isOrdered: true,
          listStyleType: 'DEC_LEN_LROM_P',
        },
      },
      type: ListTypeEnum.olist,
    },
    {
      title: '选中无序列表',
      data: {
        list: {
          listId: 'dn7ncrq6m7j',
          level: 0,
          isOrdered: false,
          listStyle: {
            format: 'bullet',
            text: '\u25cf',
            align: 'left',
          },
          listStyleType: 'SCIR_ECIR_SREC',
        },
      },
      type: ListTypeEnum.ulist,
    },
    {
      title: '选中普通段落',
      data: {},
      type: ListTypeEnum.ulist,
    },
    {
      title: '选中普通段落',
      data: {},
      type: ListTypeEnum.olist,
    },
    {
      title: '选中普通段落',
      data: {},
      type: ListTypeEnum.tlist,
    },
  ].forEach(({ title, type, data }) => {
    it(title, () => {
      const value = ((
        <value>
          <document>
            <block
              type="paragraph"
              data={data}
            >
              aaa
              <cursor />
            </block>
          </document>
        </value>
      ) as unknown) as Value;
      const controller = Controller.create({
        plugins,
        value,
      });
      const result = getActiveState(controller, type);

      if (Object.keys(data).length === 0) {
        expect(result).toBe(false);
      } else {
        expect(result).toBe(true);
      }
    });
  });
});
